body {
  margin: 0;
  padding: 0;
  background-color: #e9f0f4;
  font-family: "Segoe UI", Robot, sans-serif;
  color: #222;
  max-width: 768px;
}

.container {
  max-width: 1280px;
  margin: 0 auto;
  padding: 20px;
}

.page-content {
  background-color: #fff;
  border-radius: 12px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.05);
  padding: 20px;
  margin-top: 30px;
}

.menu-wrapper {
  background-color: #fff;
  border-radius: 12px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.05);
  margin-top: 20px;
  padding: 10px 20px;
}

main {
  padding-bottom: var(--footer-height, 70px);
}
