:root {
    --footer-height: 70px;
}

.footer-wrapper {
    position: fixed;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);

    width: 100%;
    max-width: 1280px;
    background-color: #fff;
    border-top: 1px solid #ccc;
    box-shadow: 0 -2px 6px rgba(0, 0, 0, 0.05);
    z-index: 100;
}

.footer-inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 20px;
    flex-wrap: wrap;
}

.footer-logo {
    font-weight: bold;
}

.footer-links {
    display: flex;
    gap: 20px;
}

.footer-links a {
    text-decoration: none;
    color: #333;
    transition: color 0.3s ease;
}

.footer-links a:hover {
    color: #0077cc;
}


@media (max-width: 768px) {
    .footer-inner {
        flex-direction: column;
        gap: 10px;
        text-align: center;
    }

    .footer-links {
        flex-direction: column;
        gap: 8px;
    }
}
